@import '../../../../utils/breakpoints';

.profile {
  padding: 16px 94px;
  input[type="file"] {
    display: none;
  }
  &__title {
    font-size: 28px;
    line-height: 125.3%;
    color: #192434;
    margin-bottom: 24px;
  }
  &__container {
    display: flex;
    align-items: center;
  }
  &__img {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 102px;
    height: 102px;
    background: #DF0B92;
    border-radius: 50%;
    font-family: Helvetica, sans-serif;
    font-size: 24px;
    line-height: 122%;
    color: white;
    text-transform: uppercase;
  }
  &__upload {
    font-size: 16px;
    line-height: 19px;
    color: #3861FB;
    margin: 0px 12px;
    cursor: pointer;
  }
  &__information {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, auto));
    column-gap: 16px;
  }
  &__link {
    display: block;
    font-size: 16px;
    line-height: 19px;
    color: #3861FB;
    margin-top: 16px;
    &-center {
      margin-top: 47px ;
    }
  }
  &__start {
    grid-column-start: 1;    
  }
}

@media (max-width: $mobile-width) { 
  .profile {
    animation: openPageTranslate ease-in-out 0.6s forwards;
    padding: 24px 20px;
    &__title {
      display: none;
    }
    &__link {
      &-center {
        margin-top: 10px ;
      }
    }
    &__information {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(200px, auto));
      column-gap: 16px;
    }
  }
}